    import back from '@/components/back'
	import noData from '@/components/noData'
    export default {
        components: {
			back,
			noData
        },
        data () {
            return {
				imgs: [require("../../assets/default/doctor.png"), require("../../assets/default/doctor.png")],
				id: this.$route.query.id,
                datas: {}
            }
		},
		created() {
			this.getMedicalRecord()
        },
        watch: {
            $route: {
                handler: function(val, oldVal){
                    this.getMedicalRecord()
                },
                // 深度观察监听
                deep: true
            }
        },
		methods: {
			getMedicalRecord () {
				this.$get('SecondDiagnosisAPI/MedicalRecord/GetMedicalRecordById',{
					MedicalRecordId: this.id
				}).then( m => {
					console.log(m)
					this.datas = m
				})
            },
            toGetOrder () {
                this.$bridge.callhandler('SecondTreatDetail', this.id)
            },
            toCreateReport () {
                this.$bridge.callhandler('SecondTreatReport', this.id)
            },
            toViewReport () {
                if(this.datas.MedicalReportPath) {
                    let obj = {
                        title : '报告详情',
                        linkUrl : this.datas.MedicalReportPath
                    }
                    this.$bridge.callhandler('showPdfReport', JSON.stringify(obj))
                }
                
            },
            toDetail () {
                this.$bridge.callhandler('SecondTreatCancelDetail', this.id)
            }
		},
    }



